ul.menu {
    li {
        display: inline-block;
        a {
            text-decoration: none;
            display: block;
            font-size: 1.1rem;
            border-bottom: 2px solid transparent;
            color: $color-text;
            padding: $gutter-width $gutter-width / 2;
            &.active, &:hover {
                border-bottom: 2px solid $color-secondary; } } } }
