.field {
    padding: $gutter-width / 2 0 $gutter-width / 2 0;
    label {
        display: inline-block;
        cursor: pointer; }

    input[type='text'],
    input[type='email'],
    input[type='password'],
    textarea {
        border: 0;
        background-color: #fff;
        background: linear-gradient(#fff calc(100% - 2px), transparent 2px) no-repeat border-box, linear-gradient(to right, #ff2e63 50%, #eee 50%) no-repeat border-box;
        background-size: 200% 100%, 200% 100%;
        background-position: right 0, right 0;
        background-origin: padding-box, padding-box;
        transition: all .5s ease;
        padding: 12px 0;
        font-size: 1.14rem;
        width: 100%;
        display: block;

        &:focus {
            background-position: left 0, left 0;
            outline: none; } }

    textarea {
        min-height: 20px;
        resize: none; } }
